'use client';

import { useState, useEffect } from 'react';
import { initialBooking } from '@/src/lib/booking/initialBooking';
import { Booking } from '@/src/lib/booking/BookingTypes';
import { Input } from '@/src/components/ui/input';
import { Textarea } from '@/src/components/ui/textarea';
import { Button } from '@/src/components/ui/button';
import { useUser } from '@/src/context/AuthContext';
import { format } from 'date-fns';
import { formatCurrency } from '@/src/utils/formatCurrency';
import { parseCurrencyString } from '@/src/utils/parseCurrencyString';

interface Props {
  editingBooking: Booking | null;
  stationNames: Record<string, string>;
  companyNames: Record<string, string>;
  userNames: Record<string, string>;
  packageNames: Record<string, string>;
  onSave: (data: Omit<Booking, 'id' | 'createdAt' | 'updatedAt'>) => void;
  onCancel: () => void;
}

export default function BookingForm({
  editingBooking,
  stationNames,
  companyNames,
  userNames,
  packageNames,
  onSave,
  onCancel,
}: Props) {
  const { companyId, stationId, user } = useUser();
  const [form, setForm] = useState<Omit<Booking, 'id' | 'createdAt' | 'updatedAt'>>(initialBooking);

  useEffect(() => {
    if (editingBooking) {
      const { id, createdAt, updatedAt, ...rest } = editingBooking;
      setForm(rest);
    } else {
      setForm((prev) => ({
        ...prev,
        companyId: companyId ?? '',
        stationId: stationId ?? '',
        userId: user?.uid ?? '',
      }));
    }
  }, [editingBooking, companyId, stationId, user]);

  const handleChange = (key: keyof typeof form, value: any) => {
    setForm((prev) => ({ ...prev, [key]: value }));
  };

  const handleSubmit = () => {
    if (!form.fullName || !form.phone || !form.vehicleModel) {
      alert('Please fill Full Name, Phone, and Vehicle Model.');
      return;
    }
    onSave({
      ...form,
      companyId: companyId ?? '',
      stationId: stationId ?? '',
      userId: user?.uid ?? '',
      bookingStatus: form.bookingStatus || 'draft',
    });
  };

  return (
    <div className="max-w-5xl mx-auto p-6 bg-white rounded-lg shadow space-y-10 overflow-y-auto max-h-[80vh]">
      {/* Customer Info */}
      <Section title="Customer Information">
        <TwoColumns>
          <Input placeholder="Full Name" value={form.fullName} onChange={(e) => handleChange('fullName', e.target.value)} />
          <Input placeholder="Phone" value={form.phone} onChange={(e) => handleChange('phone', e.target.value)} />
          <Input placeholder="ID Number" value={form.idNumber} onChange={(e) => handleChange('idNumber', e.target.value)} />
          <Input placeholder="Address" value={form.address} onChange={(e) => handleChange('address', e.target.value)} />
        </TwoColumns>
      </Section>

      <Crossbar />

      {/* Vehicle Info */}
      <Section title="Vehicle Information">
        <TwoColumns>
          <Input placeholder="Vehicle Model" value={form.vehicleModel} onChange={(e) => handleChange('vehicleModel', e.target.value)} />
          <Input placeholder="Vehicle Color" value={form.vehicleColor} onChange={(e) => handleChange('vehicleColor', e.target.value)} />
          <Input placeholder="VIN" value={form.vin} onChange={(e) => handleChange('vin', e.target.value)} />
          <Input placeholder="License Plate" value={form.licensePlate || ''} onChange={(e) => handleChange('licensePlate', e.target.value)} />
        </TwoColumns>
      </Section>

      <Crossbar />

      {/* Rental Period */}
      <Section title="Rental Period">
        <TwoColumns>
          <Input type="date" value={form.rentalStartDate ? format(form.rentalStartDate.toDate(), 'yyyy-MM-dd') : ''} onChange={(e) => handleChange('rentalStartDate', new Date(e.target.value))} />
          <Input placeholder="Rental Start Hour" value={form.rentalStartHour} onChange={(e) => handleChange('rentalStartHour', e.target.value)} />
          <Input type="number" placeholder="Rental Days" value={form.rentalDays} onChange={(e) => handleChange('rentalDays', Number(e.target.value))} />
          <Input type="date" value={form.rentalEndDate ? format(form.rentalEndDate.toDate(), 'yyyy-MM-dd') : ''} onChange={(e) => handleChange('rentalEndDate', new Date(e.target.value))} />
        </TwoColumns>
      </Section>

      <Crossbar />

      {/* Pricing */}
      <Section title="Pricing & Deposit">
        <TwoColumns>
          <select className="border p-2 rounded" value={form.package || ''} onChange={(e) => handleChange('package', e.target.value)}>
            <option value="">Select Package</option>
            {Object.entries(packageNames).map(([id, name]) => (
              <option key={id} value={id}>
                {name}
              </option>
            ))}
          </select>
          <Input placeholder="Base Price" value={formatCurrency(form.basePrice)} onChange={(e) => handleChange('basePrice', parseCurrencyString(e.target.value))} />
          <Input placeholder="Battery Fee" value={formatCurrency(form.batteryFee || 0)} onChange={(e) => handleChange('batteryFee', parseCurrencyString(e.target.value))} />
          <Input placeholder="Total Amount" value={formatCurrency(form.totalAmount)} onChange={(e) => handleChange('totalAmount', parseCurrencyString(e.target.value))} />
          <Input placeholder="Deposit" value={formatCurrency(form.deposit)} onChange={(e) => handleChange('deposit', parseCurrencyString(e.target.value))} />
          <Input placeholder="Remaining Balance" value={formatCurrency(form.remainingBalance)} onChange={(e) => handleChange('remainingBalance', parseCurrencyString(e.target.value))} />
        </TwoColumns>
      </Section>

      <Crossbar />

      {/* Notes */}
      <Section title="Notes">
        <Textarea placeholder="Additional Notes" value={form.note || ''} onChange={(e) => handleChange('note', e.target.value)} />
      </Section>

      {/* Actions */}
      <div className="flex justify-end gap-4 pt-4">
        <Button onClick={handleSubmit}>Save</Button>
        <Button variant="outline" onClick={onCancel}>Cancel</Button>
      </div>
    </div>
  );
}

/* Helper components */
function Section({ title, children }: { title: string; children: React.ReactNode }) {
  return (
    <div className="space-y-4">
      <h3 className="text-xl font-semibold">{title}</h3>
      {children}
    </div>
  );
}

function TwoColumns({ children }: { children: React.ReactNode }) {
  return <div className="grid grid-cols-1 md:grid-cols-2 gap-4">{children}</div>;
}

function Crossbar() {
  return <hr className="my-6 border-t border-gray-300" />;
}
