'use client';

import { useEffect, useState } from 'react';
import { useBookingData } from '@/src/hooks/useBookingData';
import { useUser } from '@/src/context/AuthContext';

import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import UserTopMenu from '@/src/components/landingpage/UserTopMenu';
import BookingForm from '@/src/components/booking/BookingForm';
import BookingTable from '@/src/components/booking/BookingTable';
import BookingSummaryCards from '@/src/components/booking/BookingSummaryCards';
import BookingSearchFilter from '@/src/components/booking/BookingSearchFilter';
import BookingPagination from '@/src/components/booking/BookingPagination';
import { exportBookingsToExcel } from '@/src/components/booking/ExportBookings';

import { Card, CardContent } from '@/src/components/ui/card';
import { Button } from '@/src/components/ui/button';
import { Badge } from '@/src/components/ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/src/components/ui/dialog';

export default function BookingManagementPage() {
  const {
    bookings,
    stationNames,
    companyNames,
    packageNames,
    userNames,
    editingBooking,
    setEditingBooking,
    saveBooking,
    deleteBooking,
    loading,
  } = useBookingData();

  const { user, role, companyId, stationId } = useUser();

  const [searchText, setSearchText] = useState('');
  const [statusFilter, setStatusFilter] = useState('All');
  const [currentPage, setCurrentPage] = useState(1);

  const pageSize = 20;
  const normalizedRole = role?.toLowerCase() ?? '';

  const filteredBookings = bookings.filter((b) => {
    const matchesRole = (() => {
      if (normalizedRole === 'admin') return true;
      if (normalizedRole === 'company_owner' || normalizedRole === 'private_provider') return b.companyId === companyId;
      if (normalizedRole === 'station_manager') return b.stationId === stationId;
      return false;
    })();

    const matchesSearch = searchText
      ? b.fullName?.toLowerCase().includes(searchText.toLowerCase()) || b.phone?.includes(searchText)
      : true;

    const matchesStatus = statusFilter === 'All'
      ? true
      : b.bookingStatus?.toLowerCase() === statusFilter.toLowerCase();

    return matchesRole && matchesSearch && matchesStatus;
  });

  const startIndex = (currentPage - 1) * pageSize;
  const paginatedBookings = filteredBookings.slice(startIndex, startIndex + pageSize);
  const totalPages = Math.ceil(filteredBookings.length / pageSize);

  const roleDisplayName: Record<string, string> = {
    admin: 'Admin - All Bookings',
    company_owner: 'Company Owner',
    private_provider: 'Private Provider',
    station_manager: 'Station Manager',
  };

  const getBadgeColor = (role: string) => {
    switch (role) {
      case 'Admin':
        return 'bg-red-100 text-red-800';
      case 'company_owner':
        return 'bg-green-100 text-green-800';
      case 'private_provider':
        return 'bg-blue-100 text-blue-800';
      case 'station_manager':
        return 'bg-purple-100 text-purple-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <UserTopMenu />

      <main className="flex-1 p-4 space-y-6 bg-gray-50">
        {/* Title + Role */}
        <div className="flex items-center justify-between">
          <h1 className="text-2xl font-bold">Booking Management</h1>
          <Badge className={`text-sm px-3 py-1 rounded-full ${getBadgeColor(normalizedRole)}`}>
            {roleDisplayName[normalizedRole] || 'User'}
          </Badge>
        </div>

        {/* Search + Filter */}
        <BookingSearchFilter
          onSearchChange={setSearchText}
          onStatusFilterChange={setStatusFilter}
        />

        {/* Summary Cards */}
        <BookingSummaryCards bookings={filteredBookings} />

        {/* Export Button */}
        <div className="flex justify-end mb-4">
          <Button onClick={() => exportBookingsToExcel(filteredBookings)}>
            Export to Excel
          </Button>
        </div>

        {/* Booking Table */}
        <Card>
          <CardContent className="p-6">
            <h2 className="text-xl font-semibold mb-4">Bookings List</h2>

            {loading ? (
              <p>Loading bookings...</p>
            ) : bookings.length > 0 ? (
              <BookingTable
                bookings={paginatedBookings}
                stationNames={stationNames}
                companyNames={companyNames}
                packageNames={packageNames}
                userNames={userNames}
                onEdit={(b) => setEditingBooking(b)}
                onDelete={(id) => deleteBooking(id)}
              />
            ) : (
              <p className="text-gray-500 text-center">No bookings found.</p>
            )}
          </CardContent>
        </Card>

        {/* Pagination */}
        {totalPages > 1 && (
          <BookingPagination
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={setCurrentPage}
          />
        )}
      </main>

      <Footer />

      {/* Booking Form Dialog */}
      <Dialog open={!!editingBooking} onOpenChange={(open) => !open && setEditingBooking(null)}>
        <DialogContent className="max-w-5xl overflow-y-auto max-h-[90vh]">
          <DialogHeader>
            <DialogTitle>{editingBooking ? 'Edit Booking' : 'New Booking'}</DialogTitle>
          </DialogHeader>

          {editingBooking && (
            <BookingForm
              editingBooking={editingBooking}
              stationNames={stationNames}
              companyNames={companyNames}
              userNames={userNames}
              packageNames={packageNames}
              onSave={async (data) => {
                await saveBooking(data);
                setEditingBooking(null);
              }}
              onCancel={() => setEditingBooking(null)}
            />
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
}
